<template>
  <div class="permission-page">
      <!-- <separator></separator> -->

    <div class="permission-page-main">
      <i class="blue"></i>
      <div class="title">
        <span>{{$t("allocation.list")}}</span>
      </div>
    <div class="search-left">
      <span>{{$t("allocation.number")}}：</span>
      <el-input v-model="search.username" :placeholder="$t('placeholder.plsAccount')" style="width:180px;margin-right:5px;"></el-input>
      <span>{{$t("allocation.role")}}：</span>
      <el-select v-model="search.roleId" clearable :placeholder="$t('placeholder.plsCharater')" style="width:190px;">
        <el-option v-for="item in roleoptions" :key="item.id" :label="item.name" :value="item.id">
        </el-option>
      </el-select>
      <!-- <span>{{$t("allocation.state")}}：</span>
      <el-select v-model="search.status" clearable :placeholder="$t('placeholder.plsStatus')"
                 style="width:190px;">
        <el-option v-for="item in statusoptions" :key="item.value" :label="item.label" :value="item.value">
        </el-option>
      </el-select>
      <el-button type="primary" @click="searchtabledata" class="search-botton">{{$t("common.search")}}</el-button> -->
    </div>
    <div class="search-middle">
      <span>{{$t("allocation.state")}}：</span>
      <el-select v-model="search.status" clearable :placeholder="$t('placeholder.plsStatus')"
                 style="width:180px;margin-right:5px;">
        <el-option v-for="item in statusoptions" :key="item.value" :label="item.label" :value="item.value">
        </el-option>
      </el-select>
      <el-button type="primary" @click="searchtabledata" class="search-botton">{{$t("common.search")}}</el-button>
    </div>
    <div class="search-right">
      <el-button @click="batchenableopen" :disabled="multipleSelection == 0">{{$t("allocation.enabled")}}</el-button>
      <el-button @click="batchdisableopen" :disabled="multipleSelection == 0">{{$t("allocation.disable")}}</el-button>
      <el-button @click="adddialogVisible = true" :disabled="!$route.meta['add']">{{$t("allocation.add")}}</el-button>
    </div>
    <br>
    <div class="table-box">
    <el-table :data="tableData"
              @selection-change="handleSelectionChange"
              style="width: 100%"
              :header-cell-style="{background:'rgba(245,248,252,1)'}">
      <el-table-column type="selection" width="55" :selectable='filterselectable' align="center">
      </el-table-column>
      <el-table-column type="index" width="70" :label="$t('allocation.index')" align="center">
      </el-table-column>
      <el-table-column prop="username" :label="$t('allocation.number')" min-width="140" align="center">
      </el-table-column>
      <!--<el-table-column prop="orgname" :label="$t('allocation.tenant')" width="170">-->
      <!--</el-table-column>-->
      <el-table-column prop="userRoles[0].name" :label="$t('allocation.role')" align="center" min-width="140">
        <template slot-scope="scope" >
          <span v-for="(value,index) in scope.row.userRoles">{{value.name}}<span v-if="index<scope.row.userRoles.length-1">，</span>  </span>
        </template>
      </el-table-column>
      <el-table-column prop="status" :label="$t('allocation.state')" align="center">
        <template slot-scope="scope">
          <span v-if="scope.row.status == 0">{{$t("common.disable")}}</span>
          <span v-else>{{$t("common.enabled")}}</span>
          <!-- <el-switch
            v-model="switchBoolean"
            active-text="启用"
            inactive-text="禁用">
          </el-switch> -->
        </template>
      </el-table-column>
      <el-table-column :label="$t('allocation.manipulation')" width="180" align="center">
        <template slot-scope="scope" >
          <!--scope.row-->
          <el-button v-if="!scope.row.userRoles.length" type="text" size="small" @click="addscopedata(scope.row)" :disabled="!$route.meta['modify']">{{$t("allocation.authorized")}}</el-button>
          <el-button v-if="scope.row.userRoles.length && scope.row.userRoles[0].id!=1" type="text" size="small" @click="addscopedata(scope.row)" :disabled="!$route.meta['modify']">{{$t("allocation.authorized")}}</el-button>
          <!-- <el-button type="text" size="small" @click="deletescopedata(scope.row)">删除角色</el-button>-->
        </template>
      </el-table-column>
      <el-table-column :label="$t('common.operation')" min-width="200" align="center">
        <template slot-scope="scope"  >
          <!-- <el-button type="text" size="small" @click="detailopen(scope.row)">详情</el-button>-->
          <el-button v-if="!scope.row.userRoles.length" type="text" size="small" @click="updateopen(scope.row)" :disabled="!$route.meta['modify']">{{$t("common.edit")}}</el-button>
          <el-button v-else-if="scope.row.userRoles.length && scope.row.userRoles[0].id!=1" type="text" size="small" @click="updateopen(scope.row)" :disabled="!$route.meta['modify']">{{$t("common.edit")}}</el-button>

          <el-button v-if="!scope.row.userRoles.length && scope.row.status == 0" type="text" size="small" @click="enableopen(scope.row)" :disabled="!$route.meta['modify']">{{$t("common.enabled")}}</el-button>
          <el-button v-if="scope.row.userRoles.length && scope.row.userRoles[0].id!=1 && scope.row.status == 0" type="text" size="small" @click="enableopen(scope.row)" :disabled="!$route.meta['modify']">{{$t("common.enabled")}}</el-button>

          <el-button v-if="!scope.row.userRoles.length && scope.row.status == 1" type="text" size="small" @click="disableopen(scope.row)" :disabled="!$route.meta['modify']">{{$t("common.disable")}}</el-button>
          <el-button v-if="scope.row.userRoles.length && scope.row.userRoles[0].id!=1 && scope.row.status == 1" type="text" size="small" @click="disableopen(scope.row)" :disabled="!$route.meta['modify']">{{$t("common.disable")}}</el-button>

          <el-button v-if="!scope.row.userRoles.length" type="text" size="small" @click="deleteopen(scope.row)" :disabled="!$route.meta['remove']">{{$t("common.delete")}}</el-button>
          <el-button v-else-if="scope.row.userRoles.length && scope.row.userRoles[0].id!=1" type="text" size="small" @click="deleteopen(scope.row)" :disabled="!$route.meta['remove']">{{$t("common.delete")}}</el-button>
        </template>
      </el-table-column>
    </el-table>
    </div>
    <el-dialog :title="$t('allocation.add')" :visible.sync="adddialogVisible" width="30%" :before-close="createFileHandleClose">
      <el-form ref="createfrom" :model="createfrom" :rules="createfromrules" label-width="150px" >
        <!--<el-form-item :label="$t('allocation.inf.mechanism')" prop="orgId">-->
          <!--<el-select v-model="createfrom.orgId" clearable :placeholder="$t('allocation.inf.organization')">-->
            <!--<el-option v-for="item in createoptions" :key="item.id" :label="item.name" :value="item.id">-->
            <!--</el-option>-->
          <!--</el-select>-->
        <!--</el-form-item>-->
        <el-form-item :label="$t('allocation.number')" prop="username">
          <el-input v-model="createfrom.username"></el-input>
        </el-form-item>
        <el-form-item :label="$t('allocation.inf.password')" prop="password">
          <el-input v-model="createfrom.password"></el-input>
        </el-form-item>
        <el-form-item :label="$t('allocation.inf.email')" prop="email">
          <el-input v-model="createfrom.email"></el-input>
        </el-form-item>
        <el-form-item :label="$t('allocation.inf.phone')" prop="mobile">
          <el-input v-model="createfrom.mobile"></el-input>
        </el-form-item>
        <el-form-item class="sub">
          <el-button type="primary" @click="cancelcreatedialogVisible('createfrom')">{{$t('common.cancel')}}</el-button>
          <el-button type="primary" @click="createdFrom('createfrom')">{{$t('common.ok')}}</el-button>
        </el-form-item>
      </el-form>
    </el-dialog>

    <el-dialog :title="$t('common.edit')"  :visible.sync="updateialogVisible" width="30%" :before-close="updateFileHandleClose">
      <el-form ref="updatefrom" :model="updatefrom" :rules="updatefromrules" label-width="150px">
        <!--<el-form-item :label="$t('allocation.inf.mechanism')" prop="orgId">-->
          <!--<el-select v-model="updatefrom.orgId" clearable :placeholder="$t('allocation.inf.organization')">-->
            <!--<el-option v-for="item in createoptions" :key="item.id" :label="item.name" :value="item.id">-->
            <!--</el-option>-->
          <!--</el-select>-->
        <!--</el-form-item>-->
        <el-form-item :label="$t('allocation.number')" prop="username">
          <el-input v-model="updatefrom.username"></el-input>
        </el-form-item>
        <el-form-item :label="$t('allocation.inf.password')" prop="password">
          <el-input v-model="updatefrom.password"></el-input>
        </el-form-item>
        <el-form-item :label="$t('allocation.inf.email')" prop="email">
          <el-input v-model="updatefrom.email"></el-input>
        </el-form-item>
        <el-form-item :label="$t('allocation.inf.phone')" prop="mobile">
          <el-input v-model="updatefrom.mobile"></el-input>
        </el-form-item>
        <!--<el-form-item :label="$t('allocation.tenant')" prop="orgName">-->
          <!--<el-input v-model="updatefrom.orgName" :disabled="true"></el-input>-->
        <!--</el-form-item>-->
        <el-form-item :label="$t('allocation.role')" prop="roleName">
          <el-input v-model="updatefrom.roleName" :disabled="true"></el-input>
        </el-form-item>
        <el-form-item :label="$t('allocation.inf.creator')" prop="createRole">
          <el-input v-model="updatefrom.createRole" :disabled="true"></el-input>
        </el-form-item>
        <el-form-item :label="$t('allocation.inf.status')" prop="status">
          <el-input  v-if="updatefrom.status == 0" :disabled="true" :placeholder="$t('allocation.common.disable')"></el-input>
          <el-input  v-else :disabled="true" :placeholder="$t('allocation.common.enabled')"></el-input>
        </el-form-item>
        <el-form-item :label="$t('allocation.inf.manager')" prop="createUserName">
          <el-input v-model="updatefrom.createUserName" :disabled="true"></el-input>
        </el-form-item>
        <el-form-item class="sub">
          <el-button type="primary" @click="cancelupdateialogVisible('updatefrom')">{{$t("common.cancel")}}</el-button>
          <el-button type="primary" @click="updateFrom('updatefrom')">{{$t("common.ok")}}</el-button>
        </el-form-item>
      </el-form>
    </el-dialog>

      <div class="block" v-if="total>0">
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :page-sizes="[5, 10, 20]"
          layout="total, sizes, prev, pager, next, jumper"
          :total=total>
        </el-pagination>
      </div>
    <el-dialog :title="$t('allocation.authorized')" :visible.sync="adddroleialogVisible" width="30%" :before-close="addroleHandleClose">
      <el-form ref="addrolefrom" :model="addrolefrom" label-width="80px">
        <el-form-item label="角色:" prop="roleId">
          <el-select v-model="addrolefrom.roleId" clearable placeholder="请选择角色">
            <el-option v-for="item in roleoptions" :key="item.id" :label="item.name" :value="item.id">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item class="sub">
          <el-button type="primary" @click="canceladdroledialogVisible">{{$t("common.cancel")}}</el-button>
          <el-button type="primary" @click="addroleFrom">{{$t("common.ok")}}</el-button>
        </el-form-item>
      </el-form>
    </el-dialog>

    <el-dialog :title="$t('delete.deleteRole')" :visible.sync="deleteroledialogVisible" width="30%">
      <div style="width:200px;margin:auto">
        <span style="font-size: 20px;">{{$t("delete.deleteRoleY")}}</span>
      </div>
      <span slot="footer" class="dialog-footer">
                <el-button @click="deleteroledialogVisible = false">{{$t("common.cancel")}}</el-button>
                <el-button type="primary" @click="deleteroleFrom">{{$t("common.ok")}}</el-button>
            </span>
    </el-dialog>

    <el-dialog :title="$t('delete.deleteZh')" :visible.sync="deletedialogVisible" width="30%">
      <div style="width:200px;margin:auto">
        <span style="font-size: 20px;">{{$t('delete.deleteZhS')}}</span>
      </div>
      <span slot="footer" class="dialog-footer">
                <el-button type="primary" @click="deleteuser">{{$t("common.ok")}}</el-button>
                <el-button @click="deletedialogVisible = false">{{$t("common.cancel")}}</el-button>
            </span>
    </el-dialog>

    <el-dialog title="账号详情" :visible.sync="detaildialogVisible" width="30%">
      <div id="detail" style="width:200px;margin:auto">
        <div>
          <label>{{$t("allocation.number")}}: </label>
          <span>{{detailrow.userName}}</span>
        </div>
        <!--<div>-->
          <!--<label>{{$t("allocation.tenant")}}: </label>-->
          <!--<span>{{detailrow.orgName}}</span>-->
        <!--</div>-->
        <div>
          <label>{{$t("allocation.role")}}: </label>
          <span>{{detailrow.roleName}}</span>
        </div>
        <div>
          <label>{{$t("allocation.inf.creator")}}: </label>
          <span>{{detailrow.createRole}}</span>
        </div>
        <div>
          <label>{{$t("allocation.inf.manager")}}: </label>
          <span v-if="detailrow.status == 0">{{$t('common.disable')}}</span>
          <span v-else>{{$t('common.enabled')}}</span>
        </div>
        <div>
          <label>{{$t("allocation.inf.manager")}}: </label>
          <span>{{detailrow.createUserName}}</span>
        </div>
        <div>
          <label>{{$t("allocation.inf.email")}}: </label>
          <span>{{detailrow.email}}</span>
        </div>
        <div>
          <label>{{$t("allocation.inf.phone")}}: </label>
          <span>{{detailrow.mobile}}</span>
        </div>
      </div>
      <div class="sub">
        <el-button type="primary" @click="detailclose">{{$t("common.close")}}</el-button>
      </div>
    </el-dialog>
    </div>
  </div>
</template>

<script>
  import separator from "../../../../public/separator.vue";
  import NetAPI from "../../../../../config/api.js";

  export default {
    components: {
      separator
    },
    data() {
      var validateUsernam = (rule, value, callback) => {
        if (value.search(/^[A-Za-z0-9]+$/)) {
          callback(new Error("请输入英文、数字"));
        } else {
          callback();
        }
      };
      var validatePassword = (rule, value, callback) => {
        if (value.search(/^[A-Za-z0-9]+$/)) {
          callback(new Error("请输入英文、数字"));
        } else {
          callback();
        }
      }
      return {
        switchBoolean: [],
        pageNo: 1,
        pageSize: 10,
        dialogContent: {
          title: "",
          content: ""
        },
        statusoptions: [
          {
            value: "1",
            label: "启用"
          },
          {
            value: "0",
            label: "禁用"
          }
        ],
        createfrom: {
          orgId: "",
          username: "",
          password: "",
          email: "",
          mobile: ""
        },
        updatefrom: {
          id: "",
          orgId: "",
          username: "",
          password: "",
          email: "",
          mobile: ""
        },
        createoptions: [],
        roleoptions: [],
        value: "",
        tableData: [],
        total: 0,
        multipleSelection: [],
        ids: 0,
        search: {
          username: "",
          roleId: "",
          status: ""
        },
        socperow: null,
        detailrow: {
          userName: "",
          orgName: "",
          roleName: "",
          createRole: "",
          status: "",
          createUserName: "",
          email: "",
          mobile: ""
        },
        addrolefrom: {
          userRoleId: "",
          roleId: "",
          id: ""
        },
        adddialogVisible: false,
        adddroleialogVisible: false,
        deleteroledialogVisible: false,
        detaildialogVisible: false,
        deletedialogVisible: false,
        updateialogVisible: false
      };
    },
    computed: {
      updatefromrules() {
        return{
          username: [
            {
              required: true,
              message: this.$t('placeholder.plsAccount'),
              trigger: "blur"
            }, {validator: this.validateUsernam, trigger: "blur"}
          ],
          password: [
            {required: true, message: this.$t('placeholder.plsPassword'), trigger: "blur"},
            {
              min: 6,
              max: 12,
              message: this.$t('placeholder.pls6Password'),
              trigger: "blur"
            }, {validator: this.validatePassword, trigger: "blur"}
          ],
          email: [
            {
              required: true,
              message: this.$t('placeholder.plsEmail'),
              trigger: "blur"
            },
            {
              type: "email",
              message: this.$t('placeholder.plsEmailC'),
              trigger: ["blur"]
            }
          ]
        }
      },
      createfromrules() {
        return{
          username: [
            {
              required: true,
              message: this.$t('placeholder.plsAccount'),
              trigger: "blur"
            }, {validator: this.validateUsernam, trigger: "blur"}
          ],
          password: [
            {required: true, message: this.$t('placeholder.plsPassword'), trigger: "blur"},
            {
              min: 6,
              max: 12,
              message: this.$t('placeholder.pls6Password'),
              trigger: "blur"
            }, {validator: this.validatePassword, trigger: "blur"}
          ],
          email: [
            {
              required: true,
              message: this.$t('placeholder.plsEmail'),
              trigger: "blur"
            },
            {
              type: "email",
              message: this.$t('placeholder.plsEmailC'),
              trigger: ["blur"]
            }
          ]
        }
      }

    },
    created() {
      this.netAPI = NetAPI().get("permissions_allocation");
      this.getData();
      // this.$http.get(this.netAPI.find, {
      //   params: {pageNo: this.pageNo, pageSize: this.pageSize}
      // }).then(
      //   res => {
      //     this.tableData = res.body.data;
      //     this.total = res.body.total;
      //     this.tableData.forEach(item => {
      //       if(item.status == 1) {
      //         this.switchBoolean = true
      //       }else{
      //         this.switchBoolean = false
      //       }
      //     })
      //   }
      // );
      this.$http
        .get(this.netAPI.findOrg, {
          params: {pageNo: 1, pageSize: 10}
        })
        .then(
          res => {
            this.createoptions = res.body.data;
          },
          err => {
          }
        );
      this.$http.get(this.netAPI.findRole).then(
        res => {
          this.roleoptions = res.body.data;
        },
        err => {
        }
      );
    },
    methods: {
      checkboxInit(row, index) {
        // console.log(index)
        if(row.username === 'admin') {
          return 0
        } else {
          return 1
        }
      },
      filterselectable(row, index) {
        if(this.$route.meta['remove']) {
          return 1;
        }else{
          return 0;
        }
      },
      handleSizeChange(val) {
        this.pageSize = val
        this.getData();
      },
      handleCurrentChange(val) {
        this.pageNo = val;
        this.getData();
      },
      getData() {
        this.netAPI = NetAPI().get("permissions_allocation");
        this.$http.get(this.netAPI.find, {
          params: {pageNo: this.pageNo, pageSize: this.pageSize}
        }).then(
          res => {
            this.tableData = res.body.data;
            this.total = res.body.total;
            this.tableData.forEach(item => {
              if(item.status == 1) {
                this.switchBoolean = true
              }else{
                this.switchBoolean = false
              }
            })
          }
        );
      },
      handleSelectionChange(val) {
        this.multipleSelection = val;
      },
      createFileHandleClose(done) {
        this.adddialogVisible = false;
        this.$refs.createfrom.resetFields();
      },
      addroleHandleClose(done) {
        this.adddroleialogVisible = false;
        this.$refs.addrolefrom.resetFields();
      },
      updateFileHandleClose(done) {
        this.updateialogVisible = false;
        this.$refs.updatefrom.resetFields();
      },
      canceladdroledialogVisible() {
        this.adddroleialogVisible = false
        this.$refs.addrolefrom.resetFields();
      },
      cancelcreatedialogVisible(formName) {
        this.$refs[formName].resetFields();
        this.adddialogVisible = false;
      },
      cancelupdateialogVisible(formName) {
        this.$refs[formName].resetFields();
        this.updateialogVisible = false;
      },
      detailopen(row) {
        this.detaildialogVisible = true;
        this.$http.get(this.netAPI.findOne + row.id).then(
          res => {
            this.detailrow = res.body.data;
          },
          err => {
          }
        );
      },

      detailclose() {
        this.detaildialogVisible = false;
      },
      searchtabledata() {
        this.$http.get(this.netAPI.find, {params: this.search}).then(
          res => {
            this.tableData = res.body.data;
            this.total = res.body.total;
            this.pageNo = 1;
          },
          err => {
          }
        );
      },
      addscopedata(row) {
        this.socperow = row;
        if (row.userRoles[0] != null) {
          this.addrolefrom.roleId = row.userRoles[0].id;
        }
        this.adddroleialogVisible = true;
      },
      deletescopedata(row) {
        this.socperow = row;
        this.deleteroledialogVisible = true;
      },
      deleteopen(row) {
        this.socperow = row;

        this.$confirm(this.$t("delete.deleteZhS"), this.$t('delete.tips'), {
          confirmButtonText: this.$t("disable.ok"),
          cancelButtonText: this.$t("disable.cancel"),
          type: 'warning'
        }).then(() => {
          this.deleteuser()
        }).catch(() => {
          this.$message({
            type: 'info',
            message: this.$t('delete.canceledDeletion')
          });
        })
      },
      updateopen(row) {
        this.$http.get(this.netAPI.findOne + row.id).then(
          res => {
            this.updatefrom = res.body.data;
            this.updateialogVisible = true;
          },
          err => {
          }
        );
      },
      getlist() {
        this.$http.get(this.netAPI.find).then(
          res => {
            this.tableData = res.body.data;
            this.total = res.body.total;
            this.pageNo = 1;
          },
          err => {
          }
        );
      },
      addroleFrom() {
        if (this.addrolefrom.roleId == null) {
          return this.$message({message: "请选中角色"});
        }
        if (this.socperow.userRoles[0] != null) {
          this.addrolefrom.userRoleId = this.socperow.userRoles[0].userRoleId;
        }
        this.addrolefrom.id = this.socperow.id;
        this.$http.post(this.netAPI.addRole, this.addrolefrom).then(
          res => {
            this.$refs.addrolefrom.resetFields();
            this.adddroleialogVisible = false;
            if (res.body.status == 200) {
              this.getlist();
            } else {
              this.$message.warning("服务错误" + res.body.message);
            }
          },
          err => {
            this.$message({
              message: "服务错误" + err,
              type: "warning"
            });
          }
        );
      },
      deleteSuccess(ids) {
        if (ids == this.multipleSelection.length) {
          this.ids = 0;
          this.$message({
            type: "success",
            message: this.$t("disable.success")
          });
          this.getlist();
        }
      },
      batchdisableopen() {
        if (this.multipleSelection.length == 0) {
          return this.$message(this.$t("disable.pleaseChoose"));
        }
        this.$confirm(this.$t("disable.disableAll"), this.$t("disable.disableUserAll"), {
          confirmButtonText: this.$t("disable.ok"),
          cancelButtonText: this.$t("disable.cancel"),
          type: "warning"
        })
          .then(() => {
            for (let i in this.multipleSelection) {
              this.$http
                .put(
                  this.netAPI.disable +
                  this.multipleSelection[i].id
                )
                .then(
                  res => {
                    if (res.body.status == 200) {
                      this.ids = this.ids + 1;

                      this.deleteSuccess(this.ids);
                    } else {
                      this.$message.warning(
                        "服务错误" + res.body.message
                      );
                    }
                  },
                  err => {
                    this.$message({
                      message: "服务错误" + err,
                      type: "warning"
                    });
                  }
                );
            }
          })
          .catch(() => {
            this.$message({
              type: "info",
              message: "已取消操作"
            });
          });
      },
      batchenableopen() {
        if (this.multipleSelection.length == 0) {
          return this.$message({message: this.$t("disable.pleaseChoose")});
        }
        this.$confirm(this.$t("disable.openAccALL"), this.$t("disable.EnablingAccountAll"), {
          confirmButtonText: this.$t("disable.ok"),
          cancelButtonText: this.$t("disable.cancel"),
          type: "warning"
        })
          .then(() => {
            for (let i in this.multipleSelection) {
              this.$http
                .put(
                  this.netAPI.enable +
                  this.multipleSelection[i].id
                )
                .then(
                  res => {
                    if (res.body.status == 200) {
                      this.ids = this.ids + 1;
                      this.deleteSuccess(this.ids);
                    } else {
                      this.$message.warning(
                        "服务错误" + res.body.message
                      );
                    }
                  },
                  err => {
                    this.$message({
                      message: "服务错误" + err,
                      type: "warning"
                    });
                  }
                );
            }
          })
          .catch(() => {
            this.$message({
              type: "info",
              message: this.$t("disable.cancelO")
            });
          });
      },
      enableopen(row) {
        this.$confirm(this.$t("disable.openAcc"), this.$t("disable.EnablingAccount"), {
          confirmButtonText: this.$t("disable.ok"),
          cancelButtonText: this.$t("disable.cancel"),
          type: "warning"
        })
          .then(() => {
            this.$http.put(this.netAPI.enable + row.id).then(
              res => {
                if (res.body.status == 200) {
                  this.$message({
                    type: "success",
                    message: this.$t("disable.success")
                  });
                  this.getlist();
                } else {
                  this.$message.warning(
                    "服务错误" + res.body.message
                  );
                }
              },
              err => {
                this.$message({
                  message: "服务错误" + err,
                  type: "warning"
                });
              }
            );
          })
          .catch(() => {
            this.$message({
              type: "info",
              message: this.$t("disable.cancelO")
            });
          });
      },
      disableopen(row) {
        this.$confirm(this.$t("disable.disableOne"), this.$t("disable.disableUser"), {
          confirmButtonText: this.$t("disable.ok"),
          cancelButtonText: this.$t("disable.cancel"),
          type: "warning"
        })
          .then(() => {
            this.$http.put(this.netAPI.disable + row.id).then(
              res => {
                if (res.body.status == 200) {
                  this.$message({
                    type: "success",
                    message: this.$t("disable.success")
                  });
                  this.getlist();
                } else {
                  this.$message.warning(
                    "服务错误" + res.body.message
                  );
                }
              },
              err => {
                this.$message({
                  message: "服务错误" + err,
                  type: "warning"
                });
              }
            );
          })
          .catch(() => {
            this.$message({
              type: "info",
              message: this.$t("disable.cancelO")
            });
          });
      },
      deleteuser() {
        this.$http.delete(this.netAPI.delete + this.socperow.id).then(
          res => {
            this.deletedialogVisible = false;
            if (res.body.status == 200) {
              this.getlist();
            } else {
              this.$message.warning("服务错误" + res.body.message);
            }
          },
          err => {
            this.$message({
              message: "服务错误" + err,
              type: "warning"
            });
          }
        );
      },
      deleteroleFrom() {
        this.$http.get(this.netAPI.deleteRole + this.socperow.id).then(
          res => {
            this.deleteroledialogVisible = false;
            if (res.body.status == 200) {
              this.getlist();
            } else {
              this.$message.warning("服务错误" + res.body.message);
            }
          },
          err => {
            this.$message({
              message: "服务错误" + err,
              type: "warning"
            });
          }
        );
      },
      updateFrom(formName) {
        this.$refs[formName].validate(valid => {
          if (valid) {
            this.$http
              .put(
                this.netAPI.put + this.updatefrom.id,
                this.updatefrom
              )
              .then(
                res => {
                  this.updatefrom = {brand_right: 0};
                  this.updateialogVisible = false;
                  if (res.body.status == 200) {
                    this.getlist();
                  } else {
                    this.$message.warning(
                      "服务错误" + res.body.message
                    );
                  }
                },
                err => {
                  this.$message({
                    message: "服务错误" + err,
                    type: "warning"
                  });
                }
              );
          } else {
            return false;
          }
        });
      },
      createdFrom(formName) {
        this.$refs[formName].validate(valid => {
          if (valid) {
            this.$http.post(this.netAPI.add, this.createfrom).then(
              res => {
                this.createfrom = {brand_right: 0};
                this.adddialogVisible = false;
                if (res.body.status == 200) {
                  this.getlist();
                } else {
                  this.$message.warning(
                    "服务错误" + res.body.message
                  );
                }
              },
              err => {
                this.$message({
                  message: "服务错误" + err,
                  type: "warning"
                });
              }
            );
          } else {
            return false;
          }
        });
      }
    }
  };
</script>

<style lang="scss" scoped>
  .sub {
    text-align: right;
  }

  input {
    border: 0px;
  }

  #detail {
    font-size: 15px;
    & > div {
      margin: 20px 0px;
    }
  }

</style>
<style scoped lang="scss">
  //分页
  .block{
    text-align: center;
    padding-top: 30px;
  }

.permission-page{
  width: 100%;
  height: 100%;
  position: relative;
  .permission-page-main{
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 30px;
    background:rgba(255,255,255,1);
    border:1px solid rgba(233,233,233,1);
    box-shadow:#D1D1D1 0 2px 4px 0;
    border-radius:5px;
    box-sizing: border-box;
    padding: 20px;
    padding-top: 60px;
      .blue{
        width:12px;
        height:20px;
        background:rgba(67,131,220,1);
        border-radius:0px 3px 3px 0px;
        position: absolute;
        left: 0;
        top: 19px;
      }
      .title{
          position: absolute;
          left: 20px;
          top: 18px;
          font-size: 20px;
          font-weight: 600;
      }
      .search-left{
        float: left;
        margin-bottom: 10px;
        margin-right: 10px;
      }
      .search-middle{
        float: left;
        margin-right: 20px;
        @media screen and(max-width:1090px){
          position: absolute;
          left: 20px;
          top: 110px;
        }
      }
      .search-right{
        float: left;
        margin-bottom: 10px;
        @media screen and(max-width:1710px){
          position: absolute;
          left: 20px;
          top: 110px;
        }
        @media screen and(max-width:1090px){
          position: absolute;
          left: 380px;
          top: 110px;
        }
      }
      .table-box{
        @media screen and(max-width:1710px){
          margin-top: 85px;
        }
      }
  }
}
</style>




